.portofolio {
  background-color: #C9E3FF;
  padding: 10% 10% 0 10%;
  z-index: 1;
}
.carousel-div {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.carousel-indicators{
  display: none;
}
.image-portofolio{
  margin: auto;
  width: 40%;
}
.text-portofolio{
  margin: auto;
  width: 60%;
}
.title_porto {
  font-size: 450%;
  color: #394989;
  text-align: center;
  font-family: 'Franklin Gothic Medium';
  line-height: 1;
  margin-bottom: 8%;
  text-decoration: underline;
  text-decoration-color: rgb(202, 117, 87);
}
.img-porto {
  max-width: 90%;
  max-height: 90%;
}
.vid-porto {
  width: 80%;
  height: 80%;
}
.p-desc-porto{
  width: 75%;
  padding-top: 3%;
  color: #394989;
  font-size: 125%;
  font-family: 'Helvetica';
}
.title-desc-porto{
  font-size: 175%;
  color: #394989;
  font-family: 'Helvetica';
}
.divider_w {
  z-index: 0;
  width: 100%;
  position: absolute;
  margin-top: 20%;
}
.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%2316277a ' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%2316277a' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

