.about {
  background-color: #C9E3FF;
  padding: 10%;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  flex-wrap: wrap;
  margin-top: 20%;
  /* margin-bottom: 20%; */
  /* padding-top: 10%; */
  /* max-height: 1300px; */
} 
.text-about{
  width: 60%;
  /* flex: 2; */
  padding: 0 5% 0 0 ;
}
.image-about{
  width: 40%;
  /* flex: 1; */
  /* background: #858585; */
  display: flex;
  justify-content: center;
  align-items: flex-end;
  /* margin: auto; */
}
.computer {
  width: 25%;
  z-index: 1;
  position: absolute;
  /* margin: auto; */
}
.sparkling {
  z-index: 0;
  width: 75%;
}
.desc_div{
  width: 85%;
}
.helo{
  width: 75%;
  color: rgb(255, 255, 255);
  font-size: 450%;
  font-family: 'Franklin Gothic Medium';
  color: #394989;
  margin-bottom: 16px;
  line-height: 1;
  text-decoration: underline;
  text-decoration-color: rgb(231, 99, 51)
}
.helo_desc{
  color: rgb(255, 255, 255);    
  font-family: 'Helvetica';
  font-size: 120%;  
  color: #394989;
}
.bg_right{
  width: 30%;
  right: 0;
  margin-top: 23%;
  float: right;
  position: absolute;
  z-index: 0;
}
.divider{
  width: 100%;
  z-index: 1;
  padding: 0;
  position: absolute;
}
.btn-download-cv{
  background-color: #394989;
  color: white;
  border: none;
}
.btn-download-cv > a{
  color: white;
  border: none;
}

@media only screen and (min-width: 340px) and (max-width: 599px) {
  .text-about{
    width: 100%;
    padding: 0 5% 0 0;
  }
  .image-about{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .computer {
    width: 45%;
    z-index: 1;
    position: absolute;
  }
  .bg_right{
    display: none;
  }
  .helo{
    color: rgb(255, 255, 255);
    font-size: 200%;
    font-family: 'Franklin Gothic Medium';
    color: #394989;
    line-height: 1;
    margin-bottom: 16px;
    text-decoration: underline;
    text-decoration-color: rgb(231, 99, 51)
  }
  .helo_desc{
    color: rgb(255, 255, 255);    
    font-family: 'Helvetica';
    font-size: 100%;  
    color: #394989;
  }
}
