.project{
  background-color: #C9E3FF;
  padding: 10%;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  flex-wrap: wrap;
} 
.text-project{
  width: 60%;
  padding: 0 5% 0 0 ;
  z-index: 2;
}
.image-project{
  padding: 0 0 0 3% ;
  width: 40%;
  /* background-color: #353b53; */
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.card-project{
  /* margin: 15px 0 0 15px; */
  max-width: 175px;
  max-height: 200px;
}
.two{
  margin-left: 5%;
}
.font_project{
  font-size: 450%;
  color: #394989;
  text-align: left;
  font-family: 'Franklin Gothic Medium';
  line-height: 1;
  margin-bottom: 16px;
  text-decoration: underline;
  text-decoration-color: rgb(231, 99, 51)
}
.desc_project{
  color: #394989;
  text-align: left;
  font-family: 'Helvetica';
  font-size: 120%;  
  font-weight: 1.5;
}
.card_project{
  font-size: 95%;
  color: rgb(0, 0, 0);
  text-align: center;
}
.parallax-effect-img {
  transform-style: preserve-3d;
  transform: perspective(1000px);
  background-size: contain;
  background-repeat: no-repeat;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  height: 100%;
  width: 100%;
  padding: 10px;
  box-shadow: 10px 10px #1c0d61;
}
.inner-element2 {
  transform: translateZ(40px);
  width: 90%;
  display: flex;
  margin: auto;
  padding-top: 9%;
}
.running_text{
  font-size: 425%;
  font-family: 'Franklin Gothic Medium';
  color: #C9E3FF;
  transform: translateX(-65px);
  position: relative;
  white-space: nowrap;
  line-height: 1;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.running_text2{
  font-size: 425%;
  font-family: 'Franklin Gothic Medium';
  color: #0e0229;
  transform: translateX(-65px);
  position: relative;
  white-space: nowrap;
  margin: 0;
  line-height: .5;
}
.moving_div{
  padding-top: 10%;
  padding-bottom: 10%;
  background-color: #C9E3FF;
}
.newspaper{
  width: 100%;
  margin-top: 5%;
  z-index: 0;
  position: absolute;
  opacity: .7;
  transform: rotateX(180deg);
}
