    /* background-color: #353b53; */
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .card-project{
    padding: 5px;
    box-sizing: border-box;
    max-width: 95px;
    max-height: 130px;
  }
  .two{
    margin-left: 0;
  }
  .font_project{
    font-size: 200%;
    color: #394989;
    text-align: left;
    font-family: 'Franklin Gothic Medium';
    line-height: 1;
    margin-bottom: 16px;
    text-decoration: underline;
    text-decoration-color: rgb(231, 99, 51)
  }
  .desc_project{
    color: #394989;
    text-align: left;
    font-family: 'Helvetica';
    font-size: 100%;  
    font-weight: 1.5;
  }
  .card_project{
    padding-top: 8%;
    font-size: 75%;
    color: rgb(0, 0, 0);
    text-align: center;
  }
  .running_text{
    display: none;
  }
  .running_text2{
    display: none;
  }
  .moving_div{
    display: none;
  }
  .newspaper{
    display: none;
  }
  
}
