.bg-footer{
  background-color: rgb(33, 28, 79);
  padding: 10% 10% 0 10%;
}
.image-footer{
  display: flex;
  justify-content: center;
  padding: 5% 10% 0 10%;
}
.footer-pict{
  width: 100%;
  position: absolute;
  z-index: 1;
  margin-top: 40%;
}
.img-contact{
  max-width: 55px;
  border-radius: 50%;
  -webkit-transition: -webkit-transform .8s ease-in-out;
  transition: transform .8s ease-in-out;
}
.img-contact:hover {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}
.div-footer{
  position: absolute;
  z-index: 2;
  margin-top: 55.5%;
  left: 37%;
}
.footer-footer{
  color: rgb(0, 1, 80);
  font: 75% 'Franklin Gothic Medium';
  background-color: bisque;
}
.name-contact{
  color: white;
}
.title-contact{
  font-size: 150%;
  color: #C9E3FF;
  font-family: 'Franklin Gothic Medium';
  line-height: 1;
  margin: 20% auto 0 auto;
  left: 0;
  right: 0;
  text-align: center;
}
@media only screen and (min-width: 360px) and (max-width: 370px) {
  .img-contact{
    max-width: 30px;
    border-radius: 50%;
    -webkit-transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
  }
  .footer-pict{
    width: 100%;
    position: absolute;
    z-index: 1;
    margin-top: 50%;
  }
  .div-footer{
    position: absolute;
    z-index: 2;
    margin-top: 65.5%;
    left: 27%;
  }
  .footer-footer{
    color: rgb(0, 1, 80);
    font: 35% 'Franklin Gothic Medium';
    margin-bottom: 0px;
  }
  .title-contact{
    font-size: 80%;
    color: #C9E3FF;
